<template>
    <div id="modalEditArticle" tabindex="-1" aria-labelledby="modalEditArticle" aria-hidden="false" class="artEdit">
        <div class="modal-dialog">
            <div class="modal-content">
                <form enctype="multipart/form-data">
                    <div class="modal-header d-flex flex-column flex-md-row align-items-center justify-content-start">
                        <p class="modal-title h5 mr-1">Editer article</p>
                        <p class="modal-title mt-1" :class="editorColor">{{editorTag}}</p>
                    </div>
                    <div class="row modal-body">
                        <div class="col-12 justify-content-center form-group">
                            <label for="editArticleTitle" class="sr-only" >Titre de l'article:</label>
                            <textarea class="form-control" v-model="editArticleTitle" id="editArticletitle" name="articleTitle" rows="2" placeholder="Votre Titre ici..."></textarea>
                        </div>
                        <div class="col-12 justify-content-center form-group">
                            <label for="editArticleChapo" class="sr-only">Sous-Titre de l'article:</label>
                            <textarea class="form-control" v-model="editArticleChapo" id="editArticleChapo" name="articleChapo" rows="4" placeholder="Votre sous-titre ici..."></textarea>
                        </div>
                        <div class="col-12 justify-content-center form-group">
                            <label for="editArticle" class="sr-only">Article :</label>
                            <textarea class="form-control" v-model="editArticle" id="editArticle" name="article" rows="10" placeholder="Votre article ici..."></textarea>
                        </div>
                        <div class="col-12 justify-content-center text-center">
                            <img :src="newImage" class="w-50 rounded">
                            <p class="small text-center">Image Ã  partager</p>
                        </div>
                        <div class="col-12 justify-content-center">
                            <div class="form-group justify-content-center">
                                <label for="File" class="sr-only">Choisir une nouvelle photo</label>
                                <input @change="onFileChange()" type="file" ref="file" name="image" class="form-control-file" id="File" accept=".jpg, .jpeg, .gif, .png">
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <div class="row w-100 justify-content-spacebetween">
                            <div class="col-6"><a href="#/articles" class="btn btn-secondary btn-block">Annuler</a></div>
                            <div class="col-6"><button type="submit" @click.prevent="updateArticle()" class="btn btn-success btn-block">Valider</button></div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios"
import router from "../router"
import Swal from "sweetalert2"


export default {
    name: "Article Edit",
    data() {
        return {
            editArticleTitle: "",
            editArticleChapo: "",
            editArticle: "",
            editUserId: "",
            newImage: "",
            editorTag: "",
            editorColor: "text-secondary",
            file: null,
            isSucces: false
        }
    },
    methods: {
        onFileChange() {
            this.file = this.$refs.file.files[0];
            this.newImage = URL.createObjectURL(this.file)
        },
        updateArticle() {
            const formData = new FormData()
            formData.set("image", this.file);
            formData.set("article", this.editArticle.toString());
            formData.set("articleTitle", this.editArticleTitle.toString());
            formData.set("articleChapo", this.editArticleChapo.toString());
            
            axios.put("http://127.0.0.1:3000/api/articles/" + this.$route.params.id, formData, { headers: { "Authorization":"Bearer " + localStorage.getItem("token")}})
            .then(res=> {
                if (res.status === 200) {
                    Swal.fire({
                        text: "L'article' Ã  Ã©tÃ© mis Ã  jour !",
                        footer: "Redirection en cours...",
                        icon: "success",
                        timer: 1000,
                        showConfirmButton: false,
                        timerProgressBar: true,
                        willClose: () => { this.file = null; router.push("/articles") }
                    })
                }
            })
            .catch(function(error) {
                const codeError = error.message.split("code ")[1]
                let messageError = ""
                switch (codeError){
                    case "400": messageError = "L'article' n'a pas Ã©tÃ© mis Ã  jour !"; break
                    case "401": messageError = "RequÃªte non-authentifiÃ©e !"; break
                }
                Swal.fire({
                    title: "Une erreur est survenue",
                    text: messageError || error.message,
                    icon: "error",
                    timer: 1500,
                    showConfirmButton: false,
                    timerProgressBar: true
                })  
            })
        }
    },
    beforeMount () {
        axios.get("http://127.0.0.1:3000/api/articles/" + this.$route.params.id, { headers: {"Authorization": "Bearer " + localStorage.getItem("token")} })
        .then(res => {
            this.editUserId = res.data.userId
            console.log(res)
            if (this.editUserId == localStorage.getItem("userId"))  {
                this.editorTag = "( Utilisateur : " + res.data.userName + " )"
                this.editArticle = res.data.article
                this.editArticleTitle = res.data.articleTitle
                this.editArticleChapo = res.data.articleChapo
                this.newImage = res.data.articleUrl
            } else if ( localStorage.getItem("role") == "true") {
                this.editorTag = "( Administrateur : " + localStorage.getItem("userName") + " )"
                this.editArticle = res.data.article
                this.editArticleTitle = res.data.articleTitle
                this.editArticleChapo = res.data.articleChapo
                this.newImage = res.data.articleUrl
                this.editorColor = "text-danger"
            } else {
                Swal.fire({
                    title: "Une erreur est survenue",
                    text: "Vous n'avez pas accÃ¨s Ã  cette fonctionnalitÃ© !",
                    icon: "error",
                    timer: 1500,
                    showConfirmButton: false,
                    timerProgressBar: true,
                    willClose: () => { router.push("/articles") }
                })  
            }
        })
        .catch(function(error) {
            const codeError = error.message.split("code ")[1]
            let messageError = ""
            switch (codeError){
                case "400": messageError = "L'article' n'a pas Ã©tÃ© mis Ã  jour !"; break
                case "401": messageError = "RequÃªte non-authentifiÃ©e !"; break
                case "404": messageError = "L'article' n'existe pas !"; break
            }
            Swal.fire({
                title: "Une erreur est survenue",
                text: messageError || error.message,
                icon: "error",
                timer: 1500,
                showConfirmButton: false,
                timerProgressBar: true,
                willClose: () => { router.push("/articles") }
            })  
        })  
    }
}
</script>

<style lang="scss">
   
.artEdit{
    padding-top: 15em;
}


</style>